/**
 * Logo Component Tests - Streamlined
 * Testing core DATEN-SEE logo functionality only
 */

import { render, screen } from '@testing-library/react';
import React from 'react';

import { Logo } from '../Logo';

describe('Logo Component', () => {
  it('should render full logo with icon and text by default', () => {
    render(<Logo />);
    
    expect(screen.getByLabelText('DATEN-SEE Logo')).toBeInTheDocument();
    expect(screen.getByText('DATEN-SEE')).toBeInTheDocument();
  });

  it('should render icon-only variant', () => {
    render(<Logo variant="icon" />);
    
    expect(screen.getByLabelText('DATEN-SEE Logo')).toBeInTheDocument();
    expect(screen.queryByText('DATEN-SEE')).not.toBeInTheDocument();
  });

  it('should render text-only variant', () => {
    render(<Logo variant="text" />);
    
    expect(screen.getByText('DATEN-SEE')).toBeInTheDocument();
    expect(screen.queryByLabelText('DATEN-SEE Logo')).not.toBeInTheDocument();
  });

  it('should be accessible with proper ARIA label', () => {
    render(<Logo variant="icon" />);
    
    const logo = screen.getByLabelText('DATEN-SEE Logo');
    expect(logo).toHaveAttribute('aria-label', 'DATEN-SEE Logo');
  });

  it('should accept custom className prop', () => {
    render(<Logo className="custom-class" />);
    
    const container = screen.getByText('DATEN-SEE').parentElement;
    expect(container).toHaveClass('custom-class');
  });
});